import React, { useState, useRef, useEffect, useCallback } from 'react';
import './Terminal.css';
import { authenticatedFetch } from '../utils/api';
import { Terminal as XTerminal } from '@xterm/xterm';
import { FitAddon } from '@xterm/addon-fit';
import { io, Socket } from 'socket.io-client';
import '@xterm/xterm/css/xterm.css';
import VoiceInterface from './VoiceInterface';
import UICommandBar from './UICommandBar';

interface TerminalCommand {
  command: string;
  timestamp: number;
}

const Terminal: React.FC = () => {
  const [isTerminalConnected, setIsTerminalConnected] = useState(false);
  const [isSupervisionOn, setIsSupervisionOn] = useState(false);
  const [isParallelAgents, setIsParallelAgents] = useState(false);
  const [isInfiniteLoop, setIsInfiniteLoop] = useState(false);
  const [isHivemindActive, setIsHivemindActive] = useState(false);
  const [isSleepMode, setIsSleepMode] = useState(false);
  const [infiniteSessionId, setInfiniteSessionId] = useState<string | null>(null);
  const [isStoppingInfinite, setIsStoppingInfinite] = useState(false);
  
  // Add state for Claude sessions
  const [claudeSessions, setClaudeSessions] = useState<Map<string, string>>(new Map());
  const [lastCommand, setLastCommand] = useState<string>('');
  
  const terminalRef = useRef<HTMLDivElement>(null);
  const xtermRef = useRef<XTerminal | null>(null);
  const socketRef = useRef<Socket | null>(null);
  const fitAddonRef = useRef<FitAddon | null>(null);
  const sessionIdRef = useRef<string>('');

  // Connect to backend Socket.IO server
  const connectToBackend = useCallback(() => {
    console.log(' Connecting to backend...');
    
    // Direct connection to backend - React proxy doesn't work with WebSockets
    const socketUrl = 'http://127.0.0.1:3000';
    
    const socket = io(socketUrl, {
      transports: ['websocket', 'polling'],
      autoConnect: true,
      reconnection: true,
      reconnectionAttempts: 5,
      reconnectionDelay: 1000,
      reconnectionDelayMax: 5000,
      timeout: 20000
    });

    socketRef.current = socket;

    socket.on('connect', () => {
      console.log(' Connected to backend Socket.IO server');
      setIsTerminalConnected(true);
      
      // Generate session ID
      sessionIdRef.current = `terminal-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
      
      // Create terminal session with detected shell
      socket.emit('terminal:create', {
        id: sessionIdRef.current,
        cols: xtermRef.current?.cols || 80,
        rows: xtermRef.current?.rows || 24
      });
    });

    socket.on('terminal:created', (data) => {
      console.log(' Terminal session created:', data);
      if (xtermRef.current) {
        xtermRef.current.clear();
        xtermRef.current.writeln('\x1b[32m Connected to terminal server\x1b[0m');
        xtermRef.current.writeln('');
      }
    });

    socket.on('terminal:data', (data) => {
      if (xtermRef.current && data.id === sessionIdRef.current) {
        xtermRef.current.write(data.data);
      }
    });

    socket.on('terminal:error', (error) => {
      console.error(' Terminal error:', error);
      if (xtermRef.current) {
        xtermRef.current.writeln(`\r\n\x1b[31m Error: ${error.message}\x1b[0m\r\n`);
      }
    });

    socket.on('terminal:exit', (data) => {
      console.log('Terminal exited:', data);
      if (xtermRef.current) {
        xtermRef.current.writeln(`\r\n\x1b[33m Terminal session ended (exit code: ${data.exitCode})\x1b[0m`);
      }
    });

    // Claude button-specific events
    socket.on('claude:output', ({ sessionId, data }) => {
      if (xtermRef.current) {
        xtermRef.current.write(data);
      }
    });

    socket.on('claude:sessionComplete', ({ sessionId, duration }) => {
      if (xtermRef.current) {
        xtermRef.current.writeln(`\r\n\x1b[32m Session completed in ${(duration / 1000).toFixed(2)}s\x1b[0m\r\n`);
      }
      
      // Remove from active sessions and reset button states
      setClaudeSessions(prev => {
        const newMap = new Map(prev);
        const sessionMode = prev.get(sessionId);
        
        // Reset button states based on session mode
        if (sessionMode === 'parallel') {
          setIsParallelAgents(false);
        } else if (sessionMode === 'supervision') {
          setIsSupervisionOn(false);
        } else if (sessionMode === 'infinite') {
          setIsInfiniteLoop(false);
          setInfiniteSessionId(null);
        }
        
        newMap.delete(sessionId);
        return newMap;
      });
    });

    socket.on('claude:error', ({ message }) => {
      if (xtermRef.current) {
        xtermRef.current.writeln(`\r\n\x1b[31m Claude Error: ${message}\x1b[0m\r\n`);
      }
    });

    socket.on('disconnect', () => {
      console.log(' Disconnected from backend');
      setIsTerminalConnected(false);
      if (xtermRef.current) {
        xtermRef.current.writeln('\r\n\x1b[31m Disconnected from server\x1b[0m');
      }
    });

    socket.on('connect_error', (error) => {
      console.error('Connection error:', error);
      if (xtermRef.current) {
        xtermRef.current.writeln('\r\n\x1b[31m Connection failed. Make sure backend is running on port 3000\x1b[0m');
      }
    });

    return socket;
  }, []);

  // Initialize real terminal
  useEffect(() => {
    const initializeTerminal = async () => {
      if (!terminalRef.current || xtermRef.current) return;

      // Create terminal instance
      const terminal = new XTerminal({
        theme: {
          background: '#1a1b26',
          foreground: '#c0caf5',
          cursor: '#c0caf5',
          black: '#15161e',
          red: '#f7768e',
          green: '#9ece6a',
          yellow: '#e0af68',
          blue: '#7aa2f7',
          magenta: '#bb9af7',
          cyan: '#7dcfff',
          white: '#a9b1d6',
          brightBlack: '#414868',
          brightRed: '#f7768e',
          brightGreen: '#9ece6a',
          brightYellow: '#e0af68',
          brightBlue: '#7aa2f7',
          brightMagenta: '#bb9af7',
          brightCyan: '#7dcfff',
          brightWhite: '#c0caf5'
        },
        fontFamily: 'Monaco, Menlo, "Courier New", monospace',
        fontSize: 14,
        lineHeight: 1.5,
        cursorBlink: true,
        allowTransparency: false,
        convertEol: true,
        screenReaderMode: false,
        macOptionIsMeta: true,
        rightClickSelectsWord: true,
        rows: 24,
        cols: 80
      });

      // Create fit addon
      const fitAddon = new FitAddon();
      terminal.loadAddon(fitAddon);

      // Open terminal
      terminal.open(terminalRef.current);
      
      // Store references
      xtermRef.current = terminal;
      fitAddonRef.current = fitAddon;

      // Fit terminal after a short delay to ensure proper sizing
      setTimeout(() => {
        fitAddon.fit();
        
        // Clear terminal and show welcome message
        terminal.clear();
        terminal.writeln('\x1b[1;36mCoder1 Terminal v2\x1b[0m');
        terminal.writeln('\x1b[2m\x1b[0m');
        terminal.writeln('');
        terminal.writeln('\x1b[33m Connecting to terminal server...\x1b[0m');
        terminal.writeln('');
        
        // Focus the terminal to enable keyboard input
        terminal.focus();
      }, 100);

      // Connect to backend
      connectToBackend();

      // Handle window resize
      const handleResize = () => {
        if (fitAddon) {
          fitAddon.fit();
        }
      };
      window.addEventListener('resize', handleResize);

      // Handle container resize (when panels are resized)
      const resizeObserver = new ResizeObserver(() => {
        if (fitAddon) {
          // Small delay to ensure DOM is updated
          setTimeout(() => {
            fitAddon.fit();
          }, 10);
        }
      });
      
      if (terminalRef.current) {
        resizeObserver.observe(terminalRef.current);
      }

      return () => {
        window.removeEventListener('resize', handleResize);
        resizeObserver.disconnect();
        if (socketRef.current) {
          socketRef.current.disconnect();
        }
        terminal.dispose();
      };
    };

    initializeTerminal();
  }, [connectToBackend]);

  // Handle terminal input (keyboard)
  useEffect(() => {
    if (!xtermRef.current || !socketRef.current) return;

    const terminal = xtermRef.current;
    const socket = socketRef.current;

    // Track command buffer
    let commandBuffer = '';

    const dataHandler = terminal.onData((data) => {
      if (sessionIdRef.current && socket.connected) {
        // Track characters in buffer FIRST before sending
        if (data === '\x7f') { // Backspace
          commandBuffer = commandBuffer.slice(0, -1);
          // Send backspace to backend
          socket.emit('terminal:data', {
            id: sessionIdRef.current,
            data: data
          });
        } else if (data === '\r' || data === '\n') {
          // Handle Enter key - check for special commands before sending to backend
          const command = commandBuffer.trim();
          
          // Handle /ui commands locally (don't send to backend)
          if (command.startsWith('/ui ')) {
            const uiCommand = command.substring(4);
            const parts = uiCommand.split(' ');
            const subcommand = parts[0];
            const args = parts.slice(1);
            
            console.log('UI command detected:', { subcommand, args });
            
            // Process UI command locally
            switch (subcommand) {
              case 'create':
                if (args.length === 0) {
                  terminal.write('\r\n\x1b[31mError: Component description required\x1b[0m\r\n');
                  terminal.write('\x1b[33mUsage: /ui create <description>\x1b[0m\r\n');
                  break;
                }
                
                const description = args.join(' ');
                if (!description.trim()) {
                  terminal.write('\r\n\x1b[31mError: Component description cannot be empty\x1b[0m\r\n');
                  break;
                }
                
                // Show generation message
                terminal.write(`\r\n Generating component: "${description}"\r\n`);
                
                // Generate component using ComponentGenerator
                import('../services/ComponentGenerator').then(({ generateBasicComponent }) => {
                  generateBasicComponent(description).then((component) => {
                    // Success message
                    terminal.write(`\x1b[32m Component generated successfully!\x1b[0m\r\n`);
                    terminal.write(` Component: ${component.name}\r\n`);
                    terminal.write(` Props: ${Object.keys(component.props).join(', ')}\r\n`);
                    
                    // Send component to Preview via postMessage
                    window.postMessage({
                      type: 'UI_COMPONENT_GENERATED',
                      component: component
                    }, '*');
                    
                  }).catch((error) => {
                    terminal.write(`\r\n\x1b[31mError: Component generation failed - ${error.message}\x1b[0m\r\n`);
                  });
                }).catch((error) => {
                  terminal.write(`\r\n\x1b[31mError: Failed to load ComponentGenerator - ${error.message}\x1b[0m\r\n`);
                });
                break;
                
              case 'help':
                const helpCommand = args[0];
                let helpMessage = '';
                
                if (helpCommand === 'create') {
                  helpMessage = `
/ui create <description> - Generate component from description

Examples:
  /ui create "button with hover effects"
  /ui create "responsive card layout"
  /ui create "modern pricing table"

Available component types:
  - button, card, input, alert
  - Supports size variants: small, medium, large
  - Supports style variants: primary, secondary, danger
`;
                } else {
                  helpMessage = `
Available /ui commands:

/ui create <description>  - Generate component from description
/ui help [command]        - Show help information  
/ui version              - Show version information

Examples:
  /ui create "button with hover effects"
  /ui create "responsive card layout"
`;
                }
                
                terminal.write('\r\n' + helpMessage + '\r\n');
                break;
                
              case 'version':
                const versionMessage = `
Coder1 Preview Enhancement v1.0.0
Phase: 1 (Basic Functionality)
Status: Active
Features: Component Generation, Live Preview
`;
                terminal.write('\r\n' + versionMessage + '\r\n');
                break;
                
              default:
                terminal.write(`\r\nUnknown UI command: ${subcommand}\r\n`);
                terminal.write('\x1b[33mUse \'/ui help\' for available commands\x1b[0m\r\n');
                break;
            }
            
            // Clear command buffer and write a new prompt
            commandBuffer = '';
            // Send a newline to show we processed the command but don't send the actual command
            terminal.write('\r\n');
            return;
          }
          
          // For non-UI commands, send to backend as normal
          socket.emit('terminal:data', {
            id: sessionIdRef.current,
            data: data
          });
          
          // Track typed commands for Claude button use
          if (command) {
            setLastCommand(command);
          }
          commandBuffer = '';
        } else if (data.charCodeAt(0) >= 32) {
          // Printable characters - add to buffer and send to backend
          commandBuffer += data;
          socket.emit('terminal:data', {
            id: sessionIdRef.current,
            data: data
          });
        } else {
          // Other control characters - just send to backend
          socket.emit('terminal:data', {
            id: sessionIdRef.current,
            data: data
          });
        }
      }
    });

    return () => {
      dataHandler.dispose();
    };
  }, [isTerminalConnected]);

  // Handle terminal resize events
  useEffect(() => {
    if (!xtermRef.current || !socketRef.current || !fitAddonRef.current) return;

    const terminal = xtermRef.current;
    const socket = socketRef.current;
    const fitAddon = fitAddonRef.current;

    // Debounce resize handler
    let resizeTimeout: NodeJS.Timeout;
    
    const resizeHandler = terminal.onResize(({ cols, rows }) => {
      clearTimeout(resizeTimeout);
      resizeTimeout = setTimeout(() => {
        if (sessionIdRef.current && socket.connected) {
          socket.emit('terminal:resize', {
            id: sessionIdRef.current,
            cols,
            rows
          });
        }
      }, 100);
    });

    return () => {
      clearTimeout(resizeTimeout);
      resizeHandler.dispose();
    };
  }, [isTerminalConnected]);

  // Get prompt for Claude buttons (use last command or default)
  const getClaudePrompt = () => {
    return lastCommand || 'Help me build a modern web application with best practices';
  };

  // Write to terminal helper
  const writeToTerminal = (text: string) => {
    if (xtermRef.current) {
      xtermRef.current.write(text);
    }
  };

  // Handle voice commands
  const handleVoiceCommand = useCallback((command: string) => {
    if (!socketRef.current || !sessionIdRef.current) {
      console.warn('Cannot send voice command: no connection');
      return;
    }
    
    // Only send if sleep mode is off
    if (!isSleepMode && socketRef.current.connected) {
      const data = command.endsWith('\r') ? command : command + '\r';
      socketRef.current.emit('terminal:data', {
        id: sessionIdRef.current,
        data: data
      });
      writeToTerminal(`\r\n\x1b[36m[Voice Command]\x1b[0m ${data.replace('\r', '')}\r\n`);
    } else {
      if (isSleepMode) {
        if (command.toLowerCase().includes('wake up') || command.toLowerCase().includes('sleep mode')) {
          writeToTerminal('\x1b[32m Launching Claude Code CLI...\x1b[0m\r\n');
          writeToTerminal('\x1b[37mClaude Code CLI would start here in a real environment\x1b[0m\r\n');
          writeToTerminal('\x1b[33m Tip: Connect to backend server on port 3000 for full functionality\x1b[0m\r\n\r\n');
          setIsSleepMode(false);
        }
      }
    }
  }, [isSleepMode]);

  // Handle voice command processing
  const processVoiceCommand = useCallback((command: string) => {
    // Log the command
    writeToTerminal(`\r\n\x1b[35m Voice: "${command}"\x1b[0m\r\n`);
    
    switch (true) {
      case command.toLowerCase().includes('run claude'):
        writeToTerminal('\x1b[32m Executing: claude\x1b[0m\r\n');
        handleVoiceCommand('claude\r');
        break;
        
      case command.toLowerCase().startsWith('type '):
        const cmd = command.substring(5);
        writeToTerminal(`\x1b[32m Executing: ${command}\x1b[0m\r\n`);
        handleVoiceCommand(cmd + '\r');
        break;
        
      case command.toLowerCase() === 'clear':
        if (xtermRef.current) {
          xtermRef.current.clear();
          writeToTerminal('\x1b[32m Terminal cleared via voice command\x1b[0m\r\n\r\n');
        }
        break;
        
      case command.toLowerCase() === 'help':
        writeToTerminal('\x1b[36m Voice Commands Help:\x1b[0m\r\n');
        writeToTerminal(' "run claude" - Launch Claude Code CLI\r\n');
        writeToTerminal(' "type [command]" - Execute any terminal command\r\n');
        writeToTerminal(' "clear" - Clear terminal screen\r\n');
        writeToTerminal(' "sleep mode" - Toggle sleep mode\r\n');
        writeToTerminal(' "supervision" - Toggle supervision mode\r\n');
        writeToTerminal(' "parallel agents" - Toggle parallel agents\r\n');
        writeToTerminal(' "infinite loop" - Toggle infinite loop mode\r\n');
        writeToTerminal(' "hivemind" - Activate hivemind collaboration\r\n\r\n');
        break;
        
      case command.toLowerCase().includes('sleep mode'):
        setIsSleepMode(!isSleepMode);
        writeToTerminal(isSleepMode ? '\x1b[33m Sleep mode deactivated\x1b[0m\r\n\r\n' : '\x1b[32m Sleep mode activated\x1b[0m\r\n\r\n');
        break;
        
      case command.toLowerCase().includes('supervision'):
        // Will be handled after button functions are defined
        writeToTerminal('\x1b[33m Toggling supervision mode...\x1b[0m\r\n');
        break;
        
      case command.toLowerCase().includes('parallel agents'):
        // Will be handled after button functions are defined
        writeToTerminal('\x1b[36m Toggling parallel agents...\x1b[0m\r\n');
        break;
        
      case command.toLowerCase().includes('infinite loop'):
        // Will be handled after button functions are defined
        writeToTerminal('\x1b[33m Toggling infinite loop...\x1b[0m\r\n');
        break;
        
      case command.toLowerCase().includes('hivemind'):
        // Will be handled after button functions are defined
        writeToTerminal('\x1b[36m Activating hivemind...\x1b[0m\r\n');
        break;
        
      default:
        writeToTerminal(`\x1b[31m Unknown voice command: ${command}\x1b[0m\r\n`);
        writeToTerminal('\x1b[33m Say "help" for available commands\x1b[0m\r\n\r\n');
    }
  }, [isSleepMode, setIsSleepMode, isTerminalConnected]);

  // Handle Supervision toggle
  const handleSupervisionToggle = async () => {
    try {
      if (!isSupervisionOn) {
        // Start supervision mode
        const prompt = getClaudePrompt();
        const sessionId = `supervision-${Date.now()}`;
        
        writeToTerminal('\r\n\x1b[33m Starting Supervision mode...\x1b[0m\r\n');
        
        // Register WebSocket for this session
        if (socketRef.current && socketRef.current.connected) {
          socketRef.current.emit('claude:button', { 
            action: 'supervision', 
            prompt, 
            sessionId 
          });
        }
        
        const response = await authenticatedFetch('/api/claude/supervision/start', {
          method: 'POST',
          body: JSON.stringify({ prompt, sessionId })
        });
        
        if (response.ok) {
          const data = await response.json();
          setClaudeSessions(prev => new Map(prev).set(data.sessionId, 'supervision'));
          setIsSupervisionOn(true);
          
          // Register socket for Claude output with the actual session ID
          if (socketRef.current && socketRef.current.connected) {
            socketRef.current.emit('claude:register', { sessionId: data.sessionId });
          }
          
          writeToTerminal(`\x1b[36mSession ID: ${data.sessionId}\x1b[0m\r\n`);
          writeToTerminal('\x1b[35m\x1b[0m\r\n');
          writeToTerminal('\x1b[35m Supervision Mode Active\x1b[0m\r\n');
          writeToTerminal('\x1b[33m All Claude tool calls will be displayed\x1b[0m\r\n');
          writeToTerminal('\x1b[33m Verbose output shows reasoning process\x1b[0m\r\n');
          writeToTerminal('\x1b[35m\x1b[0m\r\n\r\n');
        } else {
          const error = await response.json();
          writeToTerminal(`\x1b[31m Failed to start supervision: ${error.error}\x1b[0m\r\n\r\n`);
        }
      } else {
        // Stop supervision mode
        setIsSupervisionOn(false);
        writeToTerminal('\r\n\x1b[32m Supervision mode deactivated\x1b[0m\r\n\r\n');
        
        // Stop all supervision sessions
        const sessionsArray = Array.from(claudeSessions.entries());
        for (const [sessionId, mode] of sessionsArray) {
          if (mode === 'supervision') {
            try {
              await authenticatedFetch('/api/claude/session/stop', {
                method: 'POST',
                body: JSON.stringify({ sessionId })
              });
            } catch (e) {
              console.error('Error stopping session:', e);
            }
          }
        }
      }
    } catch (error) {
      console.error('Supervision toggle error:', error);
      writeToTerminal(`\r\n\x1b[31m Error: ${error}\x1b[0m\r\n\r\n`);
    }
  };

  // Handle Parallel Agents toggle
  const handleParallelAgentsToggle = async () => {
    try {
      if (!isParallelAgents) {
        // Start parallel agents
        const prompt = getClaudePrompt();
        const sessionId = `parallel-${Date.now()}`;
        
        console.log(' Prompt:', prompt);
        console.log(' Session ID:', sessionId);
        console.log(' Socket connected:', socketRef.current?.connected);
        
        writeToTerminal('\r\n\x1b[36m Starting Parallel Agents...\x1b[0m\r\n');
        
        // Register WebSocket for this session
        if (socketRef.current && socketRef.current.connected) {
          socketRef.current.emit('claude:button', { 
            action: 'parallel', 
            prompt, 
            sessionId 
          });
        }
        
        // First register the socket to receive Claude output
        if (socketRef.current && socketRef.current.connected) {
          console.log(' Registering socket for session:', sessionId);
          socketRef.current.emit('claude:register', { sessionId });
        }
        
        // Small delay to ensure registration is processed
        await new Promise(resolve => setTimeout(resolve, 100));
        
        console.log(' Calling API: /api/claude/parallel/start');
        const response = await authenticatedFetch('/api/claude/parallel/start', {
          method: 'POST',
          body: JSON.stringify({ prompt, sessionId })
        });
        console.log(' API Response:', response.status, response.ok);
        
        if (response.ok) {
          const data = await response.json();
          setClaudeSessions(prev => new Map(prev).set(data.sessionId, 'parallel'));
          setIsParallelAgents(true);
          
          writeToTerminal(`\x1b[36mSession ID: ${data.sessionId}\x1b[0m\r\n`);
          writeToTerminal('\x1b[35m\x1b[0m\r\n');
          writeToTerminal('\x1b[35m Parallel Agents Active (3 agents)\x1b[0m\r\n');
          writeToTerminal('\x1b[36m   Agent 1 - Architect: System design and structure\x1b[0m\r\n');
          writeToTerminal('\x1b[36m   Agent 2 - Implementer: Core implementation\x1b[0m\r\n');
          writeToTerminal('\x1b[36m   Agent 3 - Optimizer: Performance and best practices\x1b[0m\r\n');
          writeToTerminal('\x1b[35m\x1b[0m\r\n\r\n');
          writeToTerminal('\x1b[33mAgents are working on your task...\x1b[0m\r\n\r\n');
        } else {
          const error = await response.json();
          writeToTerminal(`\x1b[31m Failed to start parallel agents: ${error.error}\x1b[0m\r\n\r\n`);
        }
      } else {
        // Stop parallel agents
        setIsParallelAgents(false);
        writeToTerminal('\r\n\x1b[32m Parallel Agents mode deactivated\x1b[0m\r\n\r\n');
        
        // Stop all parallel sessions
        const sessionsArray = Array.from(claudeSessions.entries());
        for (const [sessionId, mode] of sessionsArray) {
          if (mode === 'parallel') {
            try {
              await authenticatedFetch('/api/claude/session/stop', {
                method: 'POST',
                body: JSON.stringify({ sessionId })
              });
            } catch (e) {
              console.error('Error stopping session:', e);
            }
          }
        }
      }
    } catch (error) {
      console.error('Parallel agents toggle error:', error);
      writeToTerminal(`\r\n\x1b[31m Error: ${error}\x1b[0m\r\n\r\n`);
    }
  };

  // Handle Infinite Loop toggle
  const handleInfiniteLoopToggle = async () => {
    try {
      if (!isInfiniteLoop) {
        // Start infinite loop - using new Claude endpoint
        const prompt = getClaudePrompt();
        const sessionId = `infinite-${Date.now()}`;
        
        writeToTerminal('\r\n\x1b[33m Starting Infinite Loop mode...\x1b[0m\r\n');
        
        // Register WebSocket for this session
        if (socketRef.current && socketRef.current.connected) {
          socketRef.current.emit('claude:button', { 
            action: 'infinite', 
            prompt, 
            sessionId 
          });
        }
        
        const response = await authenticatedFetch('/api/claude/infinite/start', {
          method: 'POST',
          body: JSON.stringify({ prompt, sessionId })
        });
        
        if (response.ok) {
          const data = await response.json();
          setInfiniteSessionId(data.sessionId);
          setClaudeSessions(prev => new Map(prev).set(data.sessionId, 'infinite'));
          setIsInfiniteLoop(true);
          
          // Register socket for Claude output with the actual session ID
          if (socketRef.current && socketRef.current.connected) {
            socketRef.current.emit('claude:register', { sessionId: data.sessionId });
          }
          
          writeToTerminal(`\x1b[36mSession ID: ${data.sessionId}\x1b[0m\r\n`);
          writeToTerminal('\x1b[35m\x1b[0m\r\n');
          writeToTerminal('\x1b[35m Infinite Loop Mode Active\x1b[0m\r\n');
          writeToTerminal('\x1b[33m Claude will iteratively improve the solution\x1b[0m\r\n');
          writeToTerminal('\x1b[33m Each iteration builds on the previous one\x1b[0m\r\n');
          writeToTerminal('\x1b[33m Click "Stop Loop" to end the process\x1b[0m\r\n');
          writeToTerminal('\x1b[35m\x1b[0m\r\n\r\n');
        } else {
          const error = await response.json();
          writeToTerminal(`\x1b[31m Failed to start infinite loop: ${error.error}\x1b[0m\r\n\r\n`);
        }
      } else {
        // Stop infinite loop
        if (infiniteSessionId) {
          setIsStoppingInfinite(true);
          writeToTerminal('\r\n\x1b[33m Stopping infinite loop...\x1b[0m\r\n');
          
          try {
            const stopResponse = await authenticatedFetch('/api/claude/session/stop', {
              method: 'POST',
              body: JSON.stringify({ sessionId: infiniteSessionId })
            });
            
            if (stopResponse.ok) {
              writeToTerminal('\r\n\x1b[32m Infinite Loop stopped successfully\x1b[0m\r\n\r\n');
            } else {
              writeToTerminal('\r\n\x1b[33m Infinite Loop stop request sent\x1b[0m\r\n\r\n');
            }
          } catch (stopError) {
            console.error('Error stopping infinite loop:', stopError);
            writeToTerminal('\r\n\x1b[33m Could not confirm stop status\x1b[0m\r\n\r\n');
          } finally {
            setIsStoppingInfinite(false);
            setInfiniteSessionId(null);
            setClaudeSessions(prev => {
              const newMap = new Map(prev);
              if (infiniteSessionId) {
                newMap.delete(infiniteSessionId);
              }
              return newMap;
            });
          }
        }
        
        setIsInfiniteLoop(false);
        writeToTerminal('\r\n\x1b[32m Infinite Loop mode deactivated\x1b[0m\r\n\r\n');
      }
    } catch (error) {
      console.error('Infinite loop toggle error:', error);
      writeToTerminal(`\r\n\x1b[31m Error: ${error}\x1b[0m\r\n\r\n`);
    }
  };

  // Handle Hivemind toggle
  const handleHivemindToggle = async () => {
    console.log(' HIVEMIND BUTTON CLICKED!');
    try {
      if (!isHivemindActive) {
        // Start Hivemind
        const prompt = getClaudePrompt();
        const sessionId = `hivemind-${Date.now()}`;
        
        writeToTerminal('\r\n\x1b[36m Starting Hivemind collaboration...\x1b[0m\r\n');
        
        // Register WebSocket for this session
        if (socketRef.current && socketRef.current.connected) {
          socketRef.current.emit('claude:button', { 
            action: 'hivemind', 
            prompt, 
            sessionId 
          });
        }
        
        const response = await authenticatedFetch('/api/claude/hivemind/start', {
          method: 'POST',
          body: JSON.stringify({ prompt, sessionId })
        });
        
        if (response.ok) {
          const data = await response.json();
          setClaudeSessions(prev => new Map(prev).set(data.sessionId, 'hivemind'));
          setIsHivemindActive(true);
          
          // Register socket for Claude output with the actual session ID
          if (socketRef.current && socketRef.current.connected) {
            socketRef.current.emit('claude:register', { sessionId: data.sessionId });
          }
          
          writeToTerminal(`\x1b[36mSession ID: ${data.sessionId}\x1b[0m\r\n`);
          writeToTerminal('\x1b[35m\x1b[0m\r\n');
          writeToTerminal('\x1b[35m Hivemind Mode Active\x1b[0m\r\n');
          writeToTerminal('\x1b[36m Phase 1: Architect designs the solution\x1b[0m\r\n');
          writeToTerminal('\x1b[36m Phase 2: Implementer builds the code\x1b[0m\r\n');
          writeToTerminal('\x1b[36m Phase 3: Reviewer validates and improves\x1b[0m\r\n');
          writeToTerminal('\x1b[35m\x1b[0m\r\n\r\n');
        } else {
          const error = await response.json();
          writeToTerminal(`\x1b[31m Failed to start hivemind: ${error.error}\x1b[0m\r\n\r\n`);
        }
      } else {
        // Stop Hivemind
        writeToTerminal('\r\n\x1b[33m Stopping Hivemind collaboration...\x1b[0m\r\n');
        
        // Find active hivemind session
        const hivemindSession = Array.from(claudeSessions.entries()).find(([id, type]) => type === 'hivemind');
        
        if (hivemindSession) {
          const [sessionId] = hivemindSession;
          
          const response = await authenticatedFetch('/api/claude/hivemind/stop', {
            method: 'POST',
            body: JSON.stringify({ sessionId })
          });
          
          if (response.ok) {
            setClaudeSessions(prev => {
              const newSessions = new Map(prev);
              newSessions.delete(sessionId);
              return newSessions;
            });
            setIsHivemindActive(false);
            writeToTerminal('\x1b[32m Hivemind collaboration stopped\x1b[0m\r\n\r\n');
          } else {
            const error = await response.json();
            writeToTerminal(`\x1b[31m Failed to stop hivemind: ${error.error}\x1b[0m\r\n\r\n`);
          }
        } else {
          setIsHivemindActive(false);
          writeToTerminal('\x1b[33m No active hivemind session found\x1b[0m\r\n\r\n');
        }
      }
    } catch (error) {
      console.error('Hivemind error:', error);
      writeToTerminal(`\r\n\x1b[31m Error: ${error}\x1b[0m\r\n\r\n`);
    }
  };

  return (
    <div className="terminal">
      <div className="terminal-header">
        <div className="terminal-header-left">
          <VoiceInterface 
            onVoiceCommand={handleVoiceCommand}
            disabled={false}
          />
        </div>
        
        <div className="terminal-header-right">
          <button 
            className={`terminal-control-btn sleep-mode ${isSleepMode ? 'active' : ''}`}
            onClick={() => setIsSleepMode(!isSleepMode)}
            style={{ zIndex: 9999, position: 'relative', pointerEvents: 'auto' }}
          >
            Sleep Mode
          </button>
          <button 
            className={`terminal-control-btn supervision ${isSupervisionOn ? 'active' : ''}`}
            onClick={handleSupervisionToggle}
            title={isSupervisionOn ? "Click to stop Supervision" : "Start Supervision mode"}
            style={{ zIndex: 9999, position: 'relative', pointerEvents: 'auto' }}
          >
            {isSupervisionOn ? 'Stop Supervision ' : 'Supervision '}
          </button>
          <button 
            className={`terminal-control-btn parallel-agents ${isParallelAgents ? 'active' : ''}`}
            onClick={handleParallelAgentsToggle}
            title={isParallelAgents ? "Click to stop Parallel Agents" : "Start Parallel Agents"}
            style={{ zIndex: 9999, position: 'relative', pointerEvents: 'auto' }}
          >
            {isParallelAgents ? 'Stop Agents ' : 'Parallel Agents '}
          </button>
          <button 
            className={`terminal-control-btn infinite-loop ${isInfiniteLoop ? 'active' : ''}`}
            onClick={handleInfiniteLoopToggle}
            disabled={isStoppingInfinite}
            title={isInfiniteLoop ? "Click to stop Infinite Loop" : "Start Infinite Loop mode"}
            style={{ zIndex: 9999, position: 'relative', pointerEvents: 'auto' }}
          >
            {isStoppingInfinite ? 'Stopping...' : isInfiniteLoop ? 'Stop Loop ' : 'Infinite Loop '}
          </button>
          <button 
            className={`terminal-control-btn hivemind ${isHivemindActive ? 'active' : ''}`}
            onClick={handleHivemindToggle}
            title={isHivemindActive ? "Click to stop Hivemind" : "Start Hivemind collaboration"}
            style={{ zIndex: 9999, position: 'relative', pointerEvents: 'auto' }}
          >
            {isHivemindActive ? 'Stop Hivemind ' : 'Hivemind '}
          </button>
        </div>
      </div>
      
      <UICommandBar />
      
      <div className="terminal-container" ref={terminalRef}></div>
    </div>
  );
};

export default Terminal;