import React, { useState, useRef, useEffect } from 'react';
import { ErrorBoundary, PreviewError, PlaceholderContent } from './ErrorBoundary';
import ComponentSandbox, { ComponentBundle } from './ComponentSandbox';
import './Preview.css';

interface PreviewState {
  mode: 'placeholder' | 'component' | 'error' | 'loading';
  currentComponent: ComponentBundle | null;
  isLoading: boolean;
  error: string | null;
}

const Preview: React.FC = () => {
  const [state, setState] = useState<PreviewState>({
    mode: 'placeholder',
    currentComponent: null,
    isLoading: false,
    error: null
  });
  
  const iframeRef = useRef<HTMLIFrameElement>(null);
  
  // Component rendering logic
  const renderComponent = async (component: ComponentBundle) => {
    setState(prev => ({ ...prev, isLoading: true, error: null, mode: 'loading' }));
    
    try {
      setState(prev => ({
        ...prev,
        mode: 'component',
        currentComponent: component,
        isLoading: false
      }));
    } catch (error: any) {
      setState(prev => ({
        ...prev,
        mode: 'error',
        error: error.message,
        isLoading: false
      }));
    }
  };
  
  // Handle errors from sandbox
  const handleSandboxError = (error: string) => {
    setState(prev => ({
      ...prev,
      mode: 'error',
      error,
      isLoading: false
    }));
  };
  
  // Handle sandbox ready
  const handleSandboxReady = () => {
    console.log('Preview sandbox is ready');
  };
  
  // Listen for component updates from terminal
  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (event.data.type === 'UI_COMPONENT_GENERATED') {
        renderComponent(event.data.component);
      }
    };
    
    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, []);
  
  // Controls for preview header
  const handleToggleMode = () => {
    setState(prev => ({
      ...prev,
      mode: prev.mode === 'placeholder' ? 'component' : 'placeholder'
    }));
  };
  
  const handleRefresh = () => {
    if (state.currentComponent) {
      renderComponent(state.currentComponent);
    }
  };
  
  return (
    <div className="preview">
      <div className="preview-header">
        <h3>Live Preview</h3>
        <div className="preview-controls">
          {state.currentComponent && (
            <button 
              className="preview-control-btn"
              onClick={handleRefresh}
              disabled={state.isLoading}
              title="Refresh component"
            >
              {state.isLoading ? '' : ''}
            </button>
          )}
          <button 
            className="preview-control-btn"
            onClick={handleToggleMode}
            title={state.mode === 'placeholder' ? 'Show last component' : 'Show placeholder'}
          >
            {state.mode === 'placeholder' ? '' : ''}
          </button>
        </div>
      </div>
      
      <div className="preview-content">
        <ErrorBoundary fallback={<PreviewError />}>
          {state.mode === 'placeholder' && <PlaceholderContent />}
          {state.mode === 'loading' && (
            <div className="preview-loading">
              <div className="loading-spinner"></div>
              <p>Rendering component...</p>
            </div>
          )}
          {state.mode === 'component' && (
            <ComponentSandbox
              ref={iframeRef}
              component={state.currentComponent}
              onError={handleSandboxError}
              onReady={handleSandboxReady}
            />
          )}
          {state.mode === 'error' && <PreviewError error={state.error || undefined} />}
        </ErrorBoundary>
        
        {state.currentComponent && (
          <div className="preview-info">
            <div className="component-info">
              <span className="component-name">
                {state.currentComponent.name}
              </span>
              <span className="component-id">
                #{state.currentComponent.id.slice(-8)}
              </span>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Preview;