import React, { useState, useEffect } from 'react';
import { fileSystemService, FileNode } from '../services/fileSystem';
import './FileExplorer.css';

interface FileExplorerProps {
  onFileSelect: (filePath: string, fileName: string) => void;
}

interface FileTreeItemProps {
  node: FileNode;
  level: number;
  onFileSelect: (filePath: string, fileName: string) => void;
}

const FileTreeItem: React.FC<FileTreeItemProps> = ({ node, level, onFileSelect }) => {
  const [isExpanded, setIsExpanded] = useState(level < 2); // Auto-expand first 2 levels

  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (node.type === 'directory') {
      setIsExpanded(!isExpanded);
    } else {
      onFileSelect(node.path, node.name);
    }
  };

  const handleToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsExpanded(!isExpanded);
  };

  return (
    <div className="file-tree-item">
      <div 
        className={`file-item ${node.type}`}
        style={{ paddingLeft: `${level * 16 + 8}px` }}
        onClick={handleClick}
        role="button"
        tabIndex={0}
      >
        {node.type === 'directory' && (
          <span 
            className={`expand-icon ${isExpanded ? 'expanded' : ''}`}
            onClick={handleToggle}
          >
            
          </span>
        )}
        <span className="file-icon">
          {fileSystemService.getFileIcon(node.name, node.type === 'directory')}
        </span>
        <span className="file-name">{node.name}</span>
      </div>
      
      {node.type === 'directory' && isExpanded && node.children && (
        <div className="file-children">
          {node.children.map((child, index) => (
            <FileTreeItem
              key={`${child.path}-${index}`}
              node={child}
              level={level + 1}
              onFileSelect={onFileSelect}
            />
          ))}
        </div>
      )}
    </div>
  );
};

const FileExplorer: React.FC<FileExplorerProps> = ({ onFileSelect }) => {
  const [fileTree, setFileTree] = useState<FileNode[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadFiles = async () => {
      try {
        const files = await fileSystemService.getProjectFiles();
        setFileTree(files);
      } catch (error) {
        console.error('Failed to load project files:', error);
      } finally {
        setLoading(false);
      }
    };

    loadFiles();
  }, []);

  if (loading) {
    return (
      <div className="file-explorer loading">
        <div className="loading-message">Loading project files...</div>
      </div>
    );
  }

  return (
    <div className="file-explorer" style={{ pointerEvents: 'auto' }}>
      <div className="file-tree" style={{ pointerEvents: 'auto' }}>
        {fileTree.map((node, index) => (
          <FileTreeItem
            key={`${node.path}-${index}`}
            node={node}
            level={0}
            onFileSelect={onFileSelect}
          />
        ))}
      </div>
    </div>
  );
};

export default FileExplorer;