import React, { forwardRef, useEffect, useState } from 'react';

interface ComponentBundle {
  id: string;
  name: string;
  code: string;
  props: Record<string, any>;
  dependencies: string[];
  generatedAt: number;
}

interface ComponentSandboxProps {
  component: ComponentBundle | null;
  onError: (error: string) => void;
  onReady?: () => void;
}

const ComponentSandbox = forwardRef<HTMLIFrameElement, ComponentSandboxProps>(
  ({ component, onError, onReady }, ref) => {
    const [sandboxReady, setSandboxReady] = useState(false);
    
    useEffect(() => {
      // Listen for sandbox messages
      const handleMessage = (event: MessageEvent) => {
        // Only accept messages from our iframe
        if (event.source !== (ref as any)?.current?.contentWindow) {
          return;
        }
        
        switch (event.data.type) {
          case 'SANDBOX_READY':
            setSandboxReady(true);
            onReady?.();
            break;
          case 'SANDBOX_ERROR':
            onError(event.data.error);
            break;
          case 'COMPONENT_RENDERED':
            console.log('Component rendered successfully:', event.data);
            break;
        }
      };
      
      window.addEventListener('message', handleMessage);
      return () => window.removeEventListener('message', handleMessage);
    }, [onError, onReady, ref]);
    
    // Send component to sandbox when ready and component changes
    useEffect(() => {
      if (sandboxReady && component && ref && (ref as any).current) {
        (ref as any).current.contentWindow?.postMessage({
          type: 'RENDER_COMPONENT',
          component
        }, '*');
      }
    }, [sandboxReady, component, ref]);
    
    const sandboxHTML = `
<!DOCTYPE html>
<html>
<head>
  <meta charset="utf-8">
  <meta name="viewport" content="width=device-width, initial-scale=1">
  <title>Component Preview</title>
  <script crossorigin src="https://unpkg.com/react@18/umd/react.development.js"></script>
  <script crossorigin src="https://unpkg.com/react-dom@18/umd/react-dom.development.js"></script>
  <script src="https://unpkg.com/@babel/standalone/babel.min.js"></script>
  <style>
    body { 
      margin: 0; 
      padding: 20px; 
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
      background: #ffffff;
      color: #24292f;
    }
    #root { 
      width: 100%; 
      height: 100%; 
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 200px;
    }
    .loading {
      color: #656d76;
      font-style: italic;
    }
    .error {
      color: #d1242f;
      background: #fff5f5;
      padding: 16px;
      border-radius: 6px;
      border: 1px solid #fd8c8c;
    }
  </style>
</head>
<body>
  <div id="root">
    <div class="loading">Loading component...</div>
  </div>
  <script>
    // Notify parent that sandbox is ready
    window.parent.postMessage({ type: 'SANDBOX_READY' }, '*');
    
    // Listen for component rendering requests
    window.addEventListener('message', (event) => {
      if (event.data.type === 'RENDER_COMPONENT') {
        try {
          renderComponent(event.data.component);
        } catch (error) {
          console.error('Component render error:', error);
          showError(error.message);
          window.parent.postMessage({ 
            type: 'SANDBOX_ERROR', 
            error: error.message 
          }, '*');
        }
      }
    });
    
    function renderComponent(component) {
      try {
        const { code, props = {}, name } = component;
        
        // Add default onClick for buttons to show they're interactive
        const enhancedProps = { ...props };
        if (name === 'Button' && !enhancedProps.onClick) {
          enhancedProps.onClick = function() {
            alert('Button clicked! \\n\\nThis button is working correctly.\\nYou can customize the onClick behavior by modifying the props.');
          };
        }
        
        // Create a safe execution context
        const componentCode = \`
          \${code}
          return \${name};
        \`;
        
        // Transform and execute the component code
        const transformedCode = Babel.transform(componentCode, {
          presets: ['react'],
          plugins: []
        }).code;
        
        // Create component constructor - don't pass props here
        const ComponentConstructor = new Function('React', transformedCode);
        const ComponentClass = ComponentConstructor(React);
        
        // Create React element with the enhanced props
        const element = React.createElement(ComponentClass, enhancedProps);
        
        // Render to DOM
        const root = ReactDOM.createRoot(document.getElementById('root'));
        root.render(element);
        
        // Notify parent of successful render
        window.parent.postMessage({
          type: 'COMPONENT_RENDERED',
          componentId: component.id,
          renderTime: Date.now() - component.generatedAt
        }, '*');
        
      } catch (error) {
        console.error('Render error:', error);
        showError('Failed to render component: ' + error.message);
        throw error;
      }
    }
    
    function showError(message) {
      const root = document.getElementById('root');
      root.innerHTML = \`
        <div class="error">
          <h4>Component Error</h4>
          <p>\${message}</p>
        </div>
      \`;
    }
    
    // Handle uncaught errors
    window.addEventListener('error', (event) => {
      const message = event.error?.message || event.message || 'Unknown error';
      showError(message);
      window.parent.postMessage({
        type: 'SANDBOX_ERROR',
        error: message
      }, '*');
    });
    
  </script>
</body>
</html>
    `;
    
    return (
      <iframe
        ref={ref}
        className="component-sandbox"
        srcDoc={sandboxHTML}
        sandbox="allow-scripts allow-same-origin"
        width="100%"
        height="400px"
        style={{ 
          border: '1px solid #e1e5e9', 
          borderRadius: '6px',
          background: 'white'
        }}
        title="Component Preview"
      />
    );
  }
);

ComponentSandbox.displayName = 'ComponentSandbox';

export default ComponentSandbox;
export type { ComponentBundle };